@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;0,800;1,400;1,700;1,800&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  background-color: rgb(240, 240, 240);
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  background-color: rgb(255, 255, 255);
  width: 95%;
  max-width: 56rem;
  margin: 14rem auto;
  padding: 4rem 2rem 4rem 2rem;
  border-radius: 2rem 2rem 10rem 2rem;
}

.formulario {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.entrada {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.entrada label {
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-family: "Poppins", sans-serif;
  color: rgb(113, 111, 111);
  font-size: 1.1rem;
  font-weight: 600;
}
.entrada input {
  font-family: "Poppins", sans-serif;
  padding: 0.8rem 1.5rem;
  width: 100%;
  border: 1px solid rgb(219, 219, 219);
  border-radius: 0.5rem;
  color: rgb(20, 20, 20);
  font-size: 2rem;
  font-weight: 800;
  outline: none;
  caret-color: rgb(133, 77, 255);
}
.entrada input:focus {
  border-color: rgb(133, 77, 255);
}

.entrada:not(:last-child) {
  margin-right: 1.6rem;
}

@media (min-width: 560px) {
  .contenedor {
    padding: 4rem 4rem 4rem 4rem;
    margin: 17rem auto;
  }
  .formulario {
    margin-bottom: 0;
    width: 34rem;
  }
}
.boton {
  position: relative;
  margin-bottom: 2rem;
}
.boton .imagen {
  margin: auto;
  display: flex;
  position: relative;
  background-color: rgb(133, 77, 255);
  width: 6.5rem;
  height: 6.5rem;
  border-radius: 50%;
  z-index: 1;
  border: none;
}
.boton .imagen img {
  width: 2.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.boton .imagen:hover {
  background-color: rgb(20, 20, 20);
  cursor: pointer;
}

.linea {
  width: 100%;
  height: 1px;
  background-color: rgb(219, 219, 219);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 560px) {
  .boton {
    margin-bottom: 0;
  }
  .imagen {
    left: 21rem;
  }
}
.resultado {
  font-family: "Poppins", sans-serif;
  font-style: italic;
  font-size: 5.5rem;
  font-weight: 900;
}
.resultado span {
  color: rgb(133, 77, 255);
}

@media (min-width: 560px) {
  .resultado {
    font-size: 6.4rem;
  }
}
.error label {
  color: rgb(255, 87, 87);
}
.error input {
  border-color: rgb(255, 87, 87);
  background-color: rgba(255, 87, 87, 0.2);
}
.error input:focus {
  border-color: rgb(255, 87, 87);
  caret-color: rgb(255, 87, 87);
}
.error .error-texto {
  color: rgb(255, 87, 87);
  font-family: "Poppins", sans-serif;
  font-style: italic;
}